import React, { useState, useEffect, useContext } from "react";
import { Line, Pie } from "react-chartjs-2";
import { MdPeople } from "react-icons/md";
import { BiChat, BiMessageCheck } from "react-icons/bi";
import { FaCogs, FaCommentDollar, FaEllipsisH } from "react-icons/fa";
import { IconContext } from "react-icons";
import { Data } from "../utils/context";
import { DashboardAnim, Indicator } from "../utils/utils";
import { DropDown } from "../utils/custom/CustomComponents";
function Dashboard() {
    const {
        apiData: {
            data: {
                date,
                charts: { facebook, instagram, website },
            },
        },
    } = useContext(Data);
    // Static Variables
    const labels = ["06:00", "12:00", "18:00", "24:00"];
    const pages = ["Website", "Facebook", "Instagram"];
    const graphPages = ["Visitors", "Live Chats", "Leads", "Sales", "Services", "Others"];
    const lineColor = ["#ed1c24", "#29b6f6", "#fdd835", "#7cb342", "#f4511e", "#7e57c2"];
    // End of Static variables

    const [main, setMain] = useState(website);
    const [page, setPage] = useState("Website");
    const [graph, setGraph] = useState("Visitors");

    const [lineGraphData, setLineGraphData] = useState({
        labels: labels,
        datasets: [
            {
                backgroundColor: "rgba(0,0,0,0.5)",
                data: [null],
            },
        ],
    });
    const [CurrentStats, setCurrentStats] = useState(main.currDayStats);
    const [Fsize, setFSize] = useState(12);

    useEffect(() => {
        switch (page) {
            case "Website":
                setMain(website);
                break;
            case "Facebook":
                setMain(facebook);
                break;
            case "Instagram":
                setMain(instagram);
                break;
            default:
                setMain(website);
        }
    }, [page, website, facebook, instagram]);

    useEffect(() => {
        setCurrentStats(main.currDayStats);
    }, [main.currDayStats, page]);
    const PieData = {
        labels: graphPages,
        datasets: [
            {
                label: "Stats",
                data: [
                    CurrentStats.visitors,
                    CurrentStats.chats,
                    CurrentStats.leads,
                    CurrentStats.sales,
                    CurrentStats.services,
                    CurrentStats.others,
                ],
                backgroundColor: lineColor,
                borderWidth: 0,
                hoverBorderWidth: 2,
            },
        ],
    };

    useEffect(() => {
        switch (graph) {
            case "Visitors":
                setLineGraph(main.visitors, lineColor[0]);
                break;
            case "Live Chats":
                setLineGraph(main.chats, lineColor[1]);
                break;
            case "Leads":
                setLineGraph(main.leads, lineColor[2]);
                break;
            case "Sales":
                setLineGraph(main.sales, lineColor[3]);
                break;
            case "Services":
                setLineGraph(main.services, lineColor[4]);
                break;
            case "Others":
                setLineGraph(main.others, lineColor[5]);
                break;
            default:
                setLineGraph(main.visitors, lineColor[0]);
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [graph, main.chats, main.leads, main.others, main.sales, main.services, main.visitors]);

    useEffect(() => {
        Indicator(250);
        if (window.innerWidth <= 500) {
            setFSize(8);
        } else {
            setFSize(12);
        }
        DashboardAnim();
        return () => {
            setGraph("");
            setMain("");
            setPage("");
        };
    }, []);

    function setLineGraph(count, bg) {
        var GrpahData = {
            labels: labels,
            datasets: [
                {
                    backgroundColor: "none",
                    data: count,
                    borderColor: bg,
                    borderWidth: 3,
                    pointBorderWidth: 1.5,
                    pointBackgroundColor: "white",
                },
            ],
        };
        setLineGraphData(GrpahData);
    }

    const options = {
        responsive: true,
        legend: { display: false },
        scales: {
            xAxes: [
                {
                    ticks: {
                        fontSize: Fsize,
                    },
                },
            ],
            yAxes: [
                {
                    ticks: {
                        fontSize: Fsize,
                    },
                },
            ],
        },
    };

    return (
        <div className="container-fluid dashboard">
            <div className="header">
                <div className="header_tag suprema">
                    <h3>Dashboard</h3>
                </div>
                <div className="options suprema">
                    <p>Show Stats for</p>
                    <DropDown
                        timeout={400}
                        header="Pages"
                        dropListArray={pages}
                        setOption={setPage}
                        customClassNames={"page-box dropDown"}
                        initialOption={page}
                    />
                </div>
            </div>
            <div className="container-fluid mt-2">
                <div className="row">
                    <div className="r-c r-c-16">
                        <h4>Overview - {page}</h4>
                    </div>
                    <div className="container-fluid graphs">
                        <div className="row row-grid mt-3">
                            <div className="card-block" style={{ zIndex: 1 }}>
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="card line-graph">
                                            <div className="d-flex justify-content-between align-items-center">
                                                <span className="card_label">Line Graph</span>
                                                <DropDown
                                                    timeout={400}
                                                    header="Graph"
                                                    dropListArray={graphPages}
                                                    setOption={setGraph}
                                                    initialOption={graph}
                                                    customClassNames="graph-box dropDown"
                                                />
                                            </div>
                                            <div className="graph pt-2">
                                                <Line data={lineGraphData} options={options} />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="card-block">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="card line-graph">
                                            <div className="d-flex justify-content-between align-items-center">
                                                <span className="card_label">Pie Chart</span>
                                            </div>
                                            <div className="graph pie-chart pt-2">
                                                <Pie
                                                    className={"pie"}
                                                    options={{
                                                        responsive: true,
                                                        legend: {
                                                            display: true,
                                                            position: "right",
                                                            labels: {
                                                                boxWidth: 20,
                                                                padding: 15,
                                                                fontFamily: "Roboto",
                                                                fontColor: "#444",
                                                                fontSize: Fsize,
                                                            },
                                                        },
                                                    }}
                                                    data={PieData}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="container-fluid">
                        <div className="row mt-4">
                            <div className="r-c r-c-16">
                                <h4>Today's Statistics - {date}</h4>
                            </div>

                            <div className="row">
                                <div className="row today mt-3">
                                    <IconContext.Provider value={{ size: "2rem" }}>
                                        <div className="box visitors">
                                            <div className="content">
                                                <div className="icon">
                                                    <MdPeople />
                                                </div>
                                                <div className="count">
                                                    <p>{graphPages[0]}</p>
                                                    <h3>{CurrentStats.visitors}</h3>
                                                </div>
                                            </div>
                                            <hr />
                                            <span className="tag">Total amount of Visitors</span>
                                        </div>
                                        <div className="box chats">
                                            <div className="content">
                                                <div className="icon">
                                                    <BiChat fill="white" />
                                                </div>
                                                <div className="count">
                                                    <p>{graphPages[1]}</p>
                                                    <h3>{CurrentStats.chats}</h3>
                                                </div>
                                            </div>
                                            <hr />
                                            <span className="tag">
                                                Total amounts of Chats initiated
                                            </span>
                                        </div>
                                        <div className="box leads">
                                            <div className="content">
                                                <div className="icon">
                                                    <BiMessageCheck />
                                                </div>
                                                <div className="count">
                                                    <p>{graphPages[2]}</p>
                                                    <h3>{CurrentStats.leads}</h3>
                                                </div>
                                            </div>
                                            <hr />
                                            <span className="tag">
                                                Toatal amount of Chats Converted to leads
                                            </span>
                                        </div>
                                        <div className="box sales">
                                            <div className="content">
                                                <div className="icon">
                                                    <FaCommentDollar />
                                                </div>
                                                <div className="count">
                                                    <p>{graphPages[3]}</p>
                                                    <h3>{CurrentStats.sales}</h3>
                                                </div>
                                            </div>
                                            <hr />
                                            <span className="tag">
                                                Total amount of Successfull Sales
                                            </span>
                                        </div>
                                        <div className="box services">
                                            <div className="content">
                                                <div className="icon">
                                                    <FaCogs />
                                                </div>
                                                <div className="count">
                                                    <p>{graphPages[4]}</p>
                                                    <h3>{CurrentStats.services}</h3>
                                                </div>
                                            </div>
                                            <hr />
                                            <span className="tag">
                                                Total amount of Services Rendered
                                            </span>
                                        </div>
                                        <div className="box others">
                                            <div className="content">
                                                <div className="icon">
                                                    <FaEllipsisH />
                                                </div>
                                                <div className="count">
                                                    <p>{graphPages[5]}</p>
                                                    <h3>{CurrentStats.others}</h3>
                                                </div>
                                            </div>
                                            <hr />
                                            <span className="tag">Other Uncatergorized Leads</span>
                                        </div>
                                    </IconContext.Provider>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Dashboard;
