import { createContext, useState, useEffect } from "react";
import { Redirect } from "react-router";

export const User = createContext({});
function Auth({ children }) {
    const [user, setUser] = useState(null);

    useEffect(() => {
        !user && <Redirect to="/login" />;
    }, [user]);

    async function login(username, password) {
        let data = await fetch("https://crimson-corp.github.io/ChatCloud_Blue_Dashboard/user.json"); // use username and password to fetch user details
        data = await data.json();
        return setUser(data); //set user in context
    }
    async function logOut() {
        //await fetch("/user.json"); // logOut user
        return setUser(null); //set user in context
    }
    return <User.Provider value={{ user, login, logOut }}>{children}</User.Provider>;
}

export default Auth;
