import React, { useState, useEffect, useRef, useContext } from "react";
import { FiSearch, FiEdit, FiMinus } from "react-icons/fi";
import { BiCaretDown } from "react-icons/bi";
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
    AccordionItemState,
} from "react-accessible-accordion";
import { Indicator, ScriptsAnim } from "../utils/utils";
import { Data } from "../utils/context";
import { Form } from "react-bootstrap";
function Scripts() {
    useEffect(() => {
        Indicator(410);
        ScriptsAnim();
    }, []);
    const {
        apiData: { queries },
    } = useContext(Data);

    const Instructions = [
        `Lorem ipsum dolor, sit amet consectetur adipisicing
        elit. Laborum consectetur cupiditate iusto repudiandae et impedit
        ratione quaerat maiores officiis dolores!`,
        `Lorem ipsum dolor, sit amet consectetur adipisicing
        elit. Laborum consectetur cupiditate iusto repudiandae et impedit
        ratione quaerat maiores officiis dolores!`,
        `Lorem ipsum dolor, sit amet consectetur adipisicing
        elit. Laborum consectetur cupiditate iusto repudiandae et impedit
        ratione quaerat maiores officiis dolores!`,
        `Lorem ipsum dolor, sit amet consectetur adipisicing
        elit. Laborum consectetur cupiditate iusto repudiandae et impedit
        ratione quaerat maiores officiis dolores!`,
        `Lorem ipsum dolor, sit amet consectetur adipisicing
        elit. Laborum consectetur cupiditate iusto repudiandae et impedit
        ratione quaerat maiores officiis dolores!`,
    ];

    var InstructionsJSX = Instructions.map((instruction, index) => {
        return (
            <div className="instruction" key={index}>
                <p>
                    <span>{index + 1}.</span> {instruction}
                </p>
            </div>
        );
    });
    return (
        <div className="container-fluid scripts">
            <div className="header_tag">
                <h3>Scripts</h3>
            </div>
            <div className="container-fluid mt-4">
                <div className="row row-grid">
                    <ScriptsTab queries={queries} />
                    <div className="p-3 section_shadow spInstruct">
                        <div className="r-c r-c-16">
                            <h5>Special Instructions</h5>
                        </div>
                        <div className="guideline-list mt-3">{InstructionsJSX}</div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Scripts;

function ScriptsTab({ queries }) {
    const [AllQueries, setAllQueries] = useState(queries);
    const [editState, setEditState] = useState(false);
    const [query_to_edit, setQuerytoEdit] = useState(null);
    const [deleteState, setDeleteState] = useState(false);
    const [query_to_delete, setQuerytoDelete] = useState({});
    const [save, setSave] = useState(false);
    const ScriptRef = useRef();

    var Queries = queries.map(({ content, label, labelId }, index) => {
        return (
            <AccordionItem key={index}>
                <AccordionItemHeading>
                    <AccordionItemButton>
                        <AccordionItemState>
                            {({ expanded }) => {
                                return (
                                    <p className="query_header bg-white shadow-sm mb-2 p-2">
                                        <BiCaretDown
                                            className={`mr-1 caret ${expanded ? "ex" : "clp"}`}
                                        />
                                        {label}
                                    </p>
                                );
                            }}
                        </AccordionItemState>
                    </AccordionItemButton>
                </AccordionItemHeading>
                <AccordionItemPanel>
                    {content.length === 0 ? (
                        <p className="text-center">No Scripts</p>
                    ) : (
                        content.map(({ text, id }, index) => {
                            return (
                                <div className="string-container" key={index}>
                                    <div className="bg-white my-1 p-2 string">
                                        <p>{text}</p>
                                        <FiEdit
                                            onClick={() => {
                                                setEditState(true);
                                                setQuerytoEdit({ text, id, labelId });
                                                ScriptRef.current.scrollTop = 0;
                                            }}
                                        />
                                        <FiMinus
                                            onClick={() => {
                                                setDeleteState(true);
                                                setEditState(false);
                                                setQuerytoDelete({ text, id, labelId });
                                                ScriptRef.current.scrollTop = 0;
                                            }}
                                        />
                                    </div>
                                </div>
                            );
                        })
                    )}
                </AccordionItemPanel>
            </AccordionItem>
        );
    });

    return (
        <div className="p-3 section_shadow script-tab">
            <div className="r-c r-c-16">
                <h5>Scripts</h5>
            </div>
            <div className="row">
                <div className="wrapper p-2 mt-3 overflow scroll" ref={ScriptRef}>
                    <div className="search shadow-sm">
                        <div className="searchbar">
                            <div className="searchBarConatiner">
                                <span className="searchIcon">
                                    <FiSearch size="16px" />
                                </span>
                                <input
                                    type="text"
                                    name="search"
                                    id="search"
                                    placeholder="Search Queries"
                                />
                            </div>
                        </div>
                    </div>
                    {save && (
                        <Save
                            props={{
                                AllQueries,
                                setSave,
                            }}
                        />
                    )}
                    {editState && (
                        <Edit
                            props={{
                                setEditState,
                                query_to_edit,
                                AllQueries,
                                setAllQueries,
                                setSave,
                            }}
                        />
                    )}
                    {deleteState && (
                        <Delete
                            props={{
                                setDeleteState,
                                query_to_delete,
                                AllQueries,
                                setAllQueries,
                                setSave,
                            }}
                        />
                    )}

                    <div className="row mt-4">
                        <div className="accordion">
                            <Accordion allowZeroExpanded={true} allowMultipleExpanded={true}>
                                {Queries}
                            </Accordion>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

function Edit({
    props: {
        query_to_edit: { id, labelId, text },
        setEditState,
        AllQueries,
        setAllQueries,
        setSave,
    },
}) {
    const [newScript, setNewScript] = useState("");
    function editQuery() {
        if (newScript === "") return;
        setAllQueries(
            AllQueries.map((queries) => {
                if (queries.labelId === labelId) {
                    queries.content.map((query) => {
                        if (query.id === id) query.text = newScript;
                        return query;
                    });
                }
                return queries;
            })
        );
        // Call Api to Edit query with the Id in parameter
        setSave(true);
        setEditState(false);
        return;
    }

    return (
        <div className="search action_wrapper edit mt-3">
            <div className="searchbar blacklight p-3">
                <p className="tag">Edit Script</p>
                <div className="searchBarConatiner">
                    <Form>
                        <Form.Group className="my-3">
                            <Form.Control
                                type="text"
                                name="edit"
                                id="search"
                                placeholder={text}
                                onChange={(e) => {
                                    setNewScript(e.target.value);
                                }}
                            />
                        </Form.Group>
                        <div className="action">
                            <input
                                type="button"
                                value="Cancel"
                                className="cancel"
                                onClick={() => {
                                    setEditState(false);
                                }}
                            />
                            <input
                                type="submit"
                                value="Edit"
                                className="edit"
                                onClick={(e) => {
                                    e.preventDefault();
                                    editQuery();
                                }}
                            />
                        </div>
                    </Form>
                </div>
            </div>
        </div>
    );
}

function Delete({
    props: {
        query_to_delete: { id, labelId, text },
        setDeleteState,
        AllQueries,
        setAllQueries,
        setSave,
    },
}) {
    function deleteQuery() {
        const NewArr = AllQueries.map((queries) => {
            if (queries.labelId === labelId) {
                var newList = queries.content.filter((query) => query.id !== id);
                queries.content = newList;
            }
            return queries;
        });
        setAllQueries(NewArr);
        // Call Api to Edit query with the Id in parameter
        setSave(true);
        setDeleteState(false);
        return;
    }

    return (
        <div className="Delete bg-white action_wrapper shadow-sm mt-3 p-3">
            <p className="tag">Delete Script?</p>
            <p className="italic my-3 query_text">{text}</p>
            <div className="action">
                <input
                    type="button"
                    value="No"
                    className="cancel"
                    onClick={() => {
                        setDeleteState(false);
                    }}
                />
                <input
                    type="submit"
                    value="Yes"
                    className="del"
                    onClick={() => {
                        deleteQuery();
                    }}
                />
            </div>
        </div>
    );
}

function Save({ props: { AllQueries, setSave } }) {
    function saveChanges() {
        // Push All Queries to API
        setSave(false);
    }
    return (
        <div className="Delete blacklight mt-3 p-2">
            <p className="tag">Save Changes ?</p>
            <div className="action mt-2">
                <input
                    type="button"
                    value="Cancel"
                    className="blackdark"
                    onClick={() => {
                        setSave(false);
                    }}
                />
                <input
                    type="submit"
                    value="Save"
                    className="save"
                    onClick={() => {
                        saveChanges();
                    }}
                />
            </div>
        </div>
    );
}
