import React, { useState, useEffect, useContext } from "react";
import Tooltip from "react-simple-tooltip";
import { Data } from "../utils/context";
import { Indicator, WidgetsAnim } from "../utils/utils";
import { Form } from "react-bootstrap";
import { DropDown } from "../utils/custom/CustomComponents";
function Widgets() {
    const {
        apiData: { widgets },
    } = useContext(Data);
    const Default = widgets;

    const [FormData, setFormData] = useState({
        agentName: Default.agentName,
        agentProfilePicture: null,
        themeColor: Default.themeColor,
        messagesFontSize: parseInt(Default.messagesFontSize),
        timestampFontSize: parseInt(Default.timestampFontSize),
        messagesPadding: parseInt(Default.messagesPadding),
        widgetPos: Default.widgetPos,
        // Dont Forget to Add  Profile image URL Steve
    });

    const [profile_file_name, setProfile_file_name] = useState("No file Chosen");
    useEffect(() => {
        Indicator(330);
        WidgetsAnim();
    }, []);

    useEffect(() => {
        FormData.agentProfilePicture && setProfile_file_name(FormData.agentProfilePicture.name);
    }, [FormData.agentProfilePicture]);

    function setData({ target: { name, value } }) {
        setFormData((prev) => {
            return { ...prev, [name]: value };
        });
    }

    function setProfilePicture(e) {
        setFormData((prev) => {
            return { ...prev, agentProfilePicture: e.target.files[0] };
        });
    }

    function submit(e) {
        e.preventDefault();
        // ^^ set Download URL in Payload object
        // send payload to API
        FormData.messagesFontSize = parseInt(FormData.messagesFontSize);
        FormData.timestampFontSize = parseInt(FormData.timestampFontSize);
        FormData.messagesPadding = parseInt(FormData.messagesPadding);
        // eslint-disable-next-line no-unused-vars
        var payload = FormData;
    }
    return (
        <div className="container-fluid">
            <div className="header_tag">
                <h3>Widgets Settings</h3>
            </div>
            <Form className="row mt-4 widget" onSubmit={submit}>
                <div className="row-grid p-0">
                    <InputBoxGen
                        props={{
                            header: "Agent Name",
                            type: "text",
                            name: "agentName",
                            placeholder: Default.agentName,
                            change: setData,
                        }}
                    />
                    <InputBoxGen
                        props={{
                            header: "Agent Profile Picture",
                            type: "file",
                            name: "agentProfilePicture",
                            change: setProfilePicture,
                            placeholder: profile_file_name,
                        }}
                    />
                    <ThemeColorSetting setFormData={setFormData} themeColor={FormData.themeColor} />
                    <InputBoxGen
                        props={{
                            header: "Messages Font Size",
                            type: "number",
                            name: "messagesFontSize",
                            placeholder: Default.messagesFontSize,
                            change: setData,
                        }}
                    />
                </div>
                <div className="row-grid p-0">
                    <InputBoxGen
                        props={{
                            header: "Time-Stamp Font-Size",
                            type: "number",
                            name: "timestampFontSize",
                            placeholder: Default.timestampFontSize,
                            change: setData,
                        }}
                    />
                    <WidgetSetting props={{ widgetPos: FormData.widgetPos, setFormData }} />
                    <InputBoxGen
                        props={{
                            header: "Messages Padding",
                            type: "number",
                            name: "messagesPadding",
                            placeholder: Default.messagesPadding,
                            change: setData,
                        }}
                    />
                    <div className="cta">
                        <input
                            className="reset"
                            type="reset"
                            value="Reset"
                            onClick={() => {
                                setFormData(Default);
                            }}
                        />
                        <input className="submit" type="button" value="Save" />
                    </div>
                </div>
            </Form>
        </div>
    );
}

function InputBoxGen({ props: { header, type, name, placeholder, change } }) {
    return (
        <div className="input_wrapper">
            <h5>{header}</h5>
            <Form.Group className="mt-3">
                <Form.Control
                    type={type}
                    name={name}
                    autoComplete="off"
                    onChange={change}
                    required={true}
                />
                <Form.Label htmlFor={name}>{placeholder}</Form.Label>
                {type === "number" && <span className="unit">px</span>}
            </Form.Group>
        </div>
    );
}

function ThemeColorSetting({ setFormData, themeColor }) {
    const [colorChange] = useState(null);
    const [color_valid, setColor_valid] = useState(false);
    var style = {
        width: 100,
        background: themeColor,
        position: "absolute",
        display: "inline",
        right: 5,
        top: 5,
        bottom: 5,
        whiteSpace: "nowrap",
    };

    useEffect(() => {
        themeColor === colorChange && setColor_valid(true);
    }, [themeColor, colorChange]);
    return (
        <div className="input_wrapper input_color">
            <h5>Theme Color</h5>
            <Form.Group className="mt-3">
                <Form.Control type="text" readOnly={true} className={`${color_valid && "valid"}`} />
                <Form.Label>{themeColor}</Form.Label>
                <Tooltip
                    content={"Click Here to Change Theme Color"}
                    customCss={style}
                    padding={8}
                    fadeDuration={300}
                    fontSize={"10px"}
                >
                    <input
                        type="color"
                        name="themeColor"
                        defaultValue={themeColor}
                        style={{ height: "100%" }}
                        onChange={(e) => {
                            setFormData((prev) => {
                                return { ...prev, themeColor: e.target.value };
                            });
                            setColor_valid(true);
                        }}
                    />
                </Tooltip>
            </Form.Group>
        </div>
    );
}

function WidgetSetting({ props: { widgetPos, setFormData } }) {
    const Positions = ["Top-left", "Top-right", "Bottom-left", "Bottom-right"];
    function select(pos) {
        setFormData((prev) => {
            return { ...prev, widgetPos: pos };
        });
    }

    return (
        <div className="input_wrapper" style={{ zIndex: 9999 }}>
            <h5>Widget Positions</h5>
            <Form.Group className="mt-3">
                <DropDown
                    customClassNames="widget_dropDown"
                    dropListArray={Positions}
                    header="Widget Positions"
                    initialOption={widgetPos}
                    setOption={select}
                    width={150}
                />
            </Form.Group>
        </div>
    );
}

export default Widgets;
