import { gsap, TimelineLite, Power4 } from "gsap";
export function Indicator(top) {
    gsap.to(".indicator", { top: `${top + "px"}`, duration: 1, ease: "Power4.Out()" });
}

export function DashboardAnim() {
    var tl = new TimelineLite({ duration: 0.25 });
    tl.from(".header_tag h3", { y: 20, autoAlpha: 0 });
    tl.from(".r-c", { x: -20, autoAlpha: 0, stagger: { each: 0.5 } }, "-=.2");
    tl.from(
        ".card",
        {
            scale: 0.8,
            autoAlpha: 0,
            stagger: { each: 0.3 },
            duration: 0.5,
            ease: "back.out(2.5)",
        },
        "-=1.5"
    );
    tl.from(".options", { x: 20, autoAlpha: 0 }, "-=1");
    tl.from(".box", {
        scale: 0.8,
        autoAlpha: 0,
        stagger: { each: 0.125 },
        ease: "back.out(2)",
    });
}

export function ConversationsAnim() {
    var tl = new TimelineLite({ duration: 0.25 });
    tl.from(".header_tag h3", { y: 20, opacity: 0 });
    tl.from(".convo_grid > div", {
        opacity: 0,
        scale: 0.9,
        transformOrigin: "center",
        stagger: { each: 0.25 },
        ease: "back.out(3)",
    });
}

export function DownloadsAnim() {
    var tl = new TimelineLite({ duration: 0.25 });
    tl.from(".header_tag h3", { y: 20, opacity: 0 });
    tl.from(".r-c", { x: -20, opacity: 0, stagger: { each: 1 } }, "-=.2");
    tl.to(".platform", { scale: 1, opacity: 1, stagger: { each: 0.125 }, ease: Power4 }, "-=1.25");
    tl.from(".manual", { opacity: 0 }, "-=.5");
    tl.from(".step", { opacity: 0, stagger: { each: 0.1 } }, "-=.25");
}

export function WidgetsAnim() {
    var tl = new TimelineLite({ duration: 0.25 });
    tl.from(".header_tag h3", { y: 20, opacity: 0 });
    tl.from("form .row-grid div", {
        autoAlpha: 0,
        scale: 0.8,
        ease: "back.out(1.5)",
        stagger: { each: 0.125 },
    });
}

export function OptionsAnim() {
    var tl = new TimelineLite({ duration: 0.2 });
    tl.from(".header_tag h3", { y: 20, opacity: 0 })
        .from(".options .connect", {
            autoAlpha: 0,
            scale: 0.9,
            ease: "back.out(2)",
        })
        .from(".options .r-c", { x: -20, opacity: 0, stagger: { each: 0.2 } }, "-=.25")
        .from(
            ".swiper_card",
            {
                opacity: 0,
                scale: 0.9,
                stagger: { each: 0.2 },
                ease: "back.out(1.7)",
            },
            "-=.25"
        )
        .from(".input-box", { autoAlpha: 0, x: -30, stagger: { each: 0.2 } }, "-=.5")
        .from(".cta", { autoAlpha: 0, ease: "back.out(1.5)" })
        .from(".swiper-button-next, .swiper-button-prev", {
            opacity: 0,
            stagger: { each: 0.2 },
        });
}

export function ScriptsAnim() {
    var tl = new TimelineLite({ duration: 0.2 });
    tl.from(".header_tag h3", { y: 20, opacity: 0 })
        .from(".scripts .script-tab, .spInstruct", {
            autoAlpha: 0,
            scale: 0.9,
            ease: "back.out(2)",
            stagger: { each: 0.25 },
        })
        .from(".scripts .r-c", { x: -20, opacity: 0, stagger: { each: 0.25 } })
        .from(".scripts .search", { opacity: 0, scale: 0.9, ease: "back.out(1.7)" })
        .from(".scripts .accordion__heading", {
            opacity: 0,
            scale: 0.9,
            stagger: { each: 0.2 },
            ease: "back.out(1.7)",
        });
}

export function FaqsAnim() {
    var tl = new TimelineLite({ duration: 0.25 });
    tl.from(".r-c", { x: -20, opacity: 0, stagger: { each: 0.5 } }, "-=.2");
    tl.from(
        ".accordion__heading",
        { opacity: 0, scale: 0.9, stagger: { each: 0.2 }, ease: "back.out(1.7)" },
        "-=.2"
    );
    tl.from(".guideline-list", { opacity: 0 }, "-=1.5");
}
