import React, { useState, useEffect, useContext } from "react";
import { FiPlus } from "react-icons/fi";
import { FaFacebookF, FaInstagram, FaLinkedinIn } from "react-icons/fa";
import { MdCheck, MdDelete, MdEdit, MdError } from "react-icons/md";
import { Swiper, SwiperSlide } from "swiper/react";
import { IconContext } from "react-icons";
import "swiper/swiper-bundle.css";
import "swiper/components/navigation/navigation.min.css";
import SwiperCore, { Navigation } from "swiper";
import "rc-dropdown/assets/index.css";
import { CSSTransition } from "react-transition-group";
import { v4 } from "uuid";
import { FaEllipsisH } from "react-icons/fa";
import { Indicator, OptionsAnim } from "../utils/utils";
import { Data } from "../utils/context";
import { Form } from "react-bootstrap";
import { Button, DropDown } from "../utils/custom/CustomComponents";
function Options() {
    useEffect(() => {
        Indicator(370);
        OptionsAnim();
    }, []);

    const {
        apiData: {
            options: {
                optionSubSettings: { domainName, domainKey, widgetState },
            },
        },
    } = useContext(Data);

    SwiperCore.use([Navigation]);

    return (
        <div className="container-fluid options">
            <div className="header_tag">
                <h3>Options</h3>
            </div>
            <div className="container-fluid mt-3">
                <div className="row-grid">
                    <div className="connect">
                        <div className="row section_shadow p-3">
                            <div className="r-c r-c-16">
                                <h4>Connect Pages</h4>
                            </div>
                            <div className="slider-row mt-3 ">
                                <div className="slider">
                                    {/* Not sure if these Connections should be rendered dynamicaly or static */}
                                    <IconContext.Provider value={{ size: 60 }}>
                                        <ConnectionCard />
                                    </IconContext.Provider>
                                </div>
                            </div>
                            <SubForm props={{ domainName, domainKey, widgetState }} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Options;

function AddLink({ props: { setAvailableCons } }) {
    const [input, setInputVal] = useState({
        name: "",
        link: "",
    });
    const [AddLinkState, setAddLinkState] = useState({
        AddPage: true,
        form: false,
        loading: false,
        feedBack: {
            state: false,
            feedback: false,
        },
    });

    function setInput({ target: { name, value } }) {
        setInputVal((prev) => {
            return { ...prev, [name]: value };
        });
    }

    function connectLink(e) {
        e.preventDefault();
        setAddLinkState((prev) => {
            return { ...prev, form: false, loading: true };
        });
        // Send request to API
        // assuming success fedback
        setAddLinkState((prev) => {
            return { ...prev, loading: false, feedBack: { state: true, feedback: true } };
        });

        var newLink = {
            id: v4(),
            link: input.link,
            name: input.name,
            icon: <FaEllipsisH />,
        };

        setAvailableCons((prev) => {
            return [...prev, newLink];
        });

        setTimeout(() => {
            setAddLinkState((prev) => {
                return { ...prev, feedBack: { ...prev.feedBack, state: false }, AddPage: true };
            });
        }, 2000);
    }
    return (
        <div className="swiper_card blacklight shadow addLinkCard">
            <CSSTransition
                in={AddLinkState.AddPage}
                classNames="conSlider"
                timeout={0}
                unmountOnExit
            >
                <div className="AddLink con d-flex flex-column align-items-center">
                    <FiPlus
                        style={{
                            strokeLinecap: "square",
                            strokeWidth: 1,
                        }}
                    />
                    <Button
                        customClassNames="addLink mt-3 py-2"
                        execute={() => {
                            setAddLinkState((prev) => {
                                return { ...prev, form: true, AddPage: false };
                            });
                        }}
                        text="Add new Link"
                    />
                </div>
            </CSSTransition>
            <CSSTransition in={AddLinkState.form} classNames="conSlider" timeout={0} unmountOnExit>
                <Form className="con form" onSubmit={connectLink}>
                    <Form.Group>
                        <Form.Control type="text" name="name" required onChange={setInput} />
                        <Form.Label htmlFor="name">Name</Form.Label>
                    </Form.Group>
                    <Form.Group>
                        <Form.Control type="text" required name="link" onChange={setInput} />
                        <Form.Label>Link</Form.Label>
                    </Form.Group>
                    <Button type="submit" customClassNames="submit" text="Connect" />
                    <Button
                        type="button"
                        customClassNames="cancel"
                        text="Cancel"
                        execute={(e) => {
                            e.preventDefault();
                            setAddLinkState((prev) => {
                                return { ...prev, form: false, AddPage: true };
                            });
                        }}
                    />
                </Form>
            </CSSTransition>
            <CSSTransition
                in={AddLinkState.loading}
                classNames="conSlider"
                timeout={0}
                unmountOnExit
            >
                <div className="cardLoading text-center con">
                    <span className="spinner-grow spinner-grow"></span>
                    <p>Connecting...</p>
                </div>
            </CSSTransition>
            <CSSTransition
                in={AddLinkState.feedBack.state}
                classNames="conSlider"
                timeout={0}
                unmountOnExit
            >
                <div className="text-center con text-center d-flex flex-column align-items-center">
                    {AddLinkState.feedBack.feedback ? (
                        <>
                            <MdCheck />
                            <p> Connected!</p>
                        </>
                    ) : (
                        <>
                            <MdError />
                            <p>Failed to Connected!</p>
                        </>
                    )}
                </div>
            </CSSTransition>
        </div>
    );
}

function ConnectionCard() {
    const [AvailableCon, setAvailableCons] = useState([
        {
            id: 1,
            link: "",
            name: "Facebook",
            conStatus: false,
            icon: <FaFacebookF />,
        },
        {
            id: 2,
            link: "",
            name: "Instagram",
            conStatus: false,
            icon: <FaInstagram />,
        },
        {
            id: 3,
            link: "",
            name: "Linkedin",
            conStatus: false,
            icon: <FaLinkedinIn />,
        },
    ]);

    const Cards = AvailableCon.map(({ id, link, name, conStatus, icon, color }) => {
        return (
            <SwiperSlide key={id}>
                <div className={`swiper_card shadow ${name}`}>
                    <div className="con_Status">
                        <p className={`indicator ${conStatus ? "Connected" : "Disconnected"}`}></p>
                        <p>{conStatus ? "Connected" : "Disconnected"}</p>
                    </div>
                    <div className="actions">
                        <MdEdit size={15} />
                        <MdDelete
                            className="delete"
                            size={15}
                            onClick={() => {
                                setAvailableCons(
                                    AvailableCon.filter((con) => {
                                        return con.id !== id;
                                    })
                                );
                            }}
                        />
                    </div>
                    <div className="icon">{icon}</div>
                    <p className="con_tag">
                        {conStatus ? "Disconnect from " : "Connect to "}
                        {name}
                    </p>

                    <button
                        className={`con_btn ${conStatus ? "disconnect" : "connect"}`}
                        onClick={() => {
                            if (!conStatus) {
                                setAvailableCons(
                                    AvailableCon.map((con) => {
                                        if (con.id === id) con.conStatus = true;
                                        return con;
                                    })
                                );
                            } else {
                                setAvailableCons(
                                    AvailableCon.map((con) => {
                                        if (con.id === id) con.conStatus = false;
                                        return con;
                                    })
                                );
                            }
                        }}
                    >
                        {conStatus ? "Disconnect" : "Connect"}
                    </button>
                </div>
            </SwiperSlide>
        );
    });
    return (
        <Swiper slidesPerView={"auto"} navigation>
            {Cards}
            <SwiperSlide>
                <AddLink props={{ setAvailableCons }} />
            </SwiperSlide>
        </Swiper>
    );
}

function SubForm({ props: { domainName, domainKey, widgetState } }) {
    const [SubFormState, setSubFormState] = useState({
        domainName: domainName,
        domainKey: domainKey,
        widgetState: widgetState,
    });

    function submitOptions(e) {
        e.preventDefault();
        // eslint-disable-next-line no-unused-vars
        var payload = SubFormState;
        // fetch logic to submit and save settings to API
        return;
    }

    function select(option) {
        setSubFormState((prev) => {
            return { ...prev, widgetState: option };
        });
    }

    function setSubForm({ target: { name, value } }) {
        setSubFormState((prev) => {
            return {
                ...prev,
                [name]: value,
            };
        });
    }
    const enabled = ["Enabled", "Disbaled"];

    return (
        <Form onSubmit={submitOptions} className="subForm">
            <div className="row mt-4 form_grid">
                <div className="">
                    <div className="r-c r-c-16">
                        <h4>Domain Settings</h4>
                    </div>
                    <div className="my-3 ml-2 input-box input_wrapper">
                        <h5 className="tag ">Domain name</h5>
                        <Form.Group className="mt-3">
                            <Form.Control
                                type="text"
                                name="domainName"
                                className="px-3"
                                onChange={setSubForm}
                                required={true}
                            />
                            <Form.Label htmlFor="domainName">{domainName}</Form.Label>
                        </Form.Group>
                    </div>
                    <div className="my-3 ml-2 input-box input_wrapper">
                        <h5 className="tag ">Domain Key</h5>
                        <Form.Group className="mt-3">
                            <Form.Control
                                type="text"
                                name="domainKey"
                                className="px-3"
                                onChange={setSubForm}
                                required={true}
                            />
                            <Form.Label htmlFor="domainName">{domainKey}</Form.Label>
                        </Form.Group>
                    </div>
                </div>
                <div className="">
                    <div className="r-c r-c-16">
                        <h4>Widget Options</h4>
                    </div>
                    <div className="my-3 ml-2 input-box">
                        <h5 className="tag">Enable/Disable Widget</h5>
                        <Form.Group className="mt-3">
                            <DropDown
                                dropListArray={enabled}
                                header="Toggle Widget"
                                initialOption={SubFormState.widgetState}
                                customClassNames="drop px-3 widget_toggle"
                                setOption={select}
                                width={150}
                            />
                        </Form.Group>
                    </div>
                    <div className="cta ">
                        <button type="button">
                            <span>Save</span>
                        </button>
                    </div>
                </div>
            </div>
        </Form>
    );
}
