import React, { useContext, useState, useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import Conversations from "./Panels/Conversations";
import Dashboard from "./Panels/Dashboard";
import Widgets from "./Panels/Widgets";
import Options from "./Panels/Options";
import Scripts from "./Panels/Scripts";
import Statistics from "./Panels/Statistics";
import DownloadPlugins from "./Panels/DownloadPlugins";
import Faqs from "./Panels/Faqs";
import { Data } from "./utils/context";
import { gsap } from "gsap";

function Main() {
    const [apiData, setApiData] = useState({});
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function fetchCall() {
            var data = await fetch(
                "https://crimson-corp.github.io/ChatCloud_Blue_Dashboard/api.json"
            );
            data = await data.json();
            setApiData(data);
            gsap.from(".panel", { opacity: 0, duration: 0.5 });
            setLoading(false);
        }
        setTimeout(() => {
            fetchCall();
        }, 1000);
    }, []);

    return (
        <>
            <Data.Provider value={{ apiData, loading }}>
                {loading ? (
                    <div className="loading">
                        <span className="spinner-grow spinner-grow"></span>
                        <h1>Fetching Data...</h1>
                    </div>
                ) : (
                    <div className="row">
                        <div>
                            <Switch>
                                <Route path="/Conversations" exact>
                                    <Conversations />
                                </Route>
                                <Route path="/Widgets" exact>
                                    <Widgets />
                                </Route>
                                <Route path="/Options">
                                    <Options />
                                </Route>

                                <Route path="/Scripts">
                                    <Scripts />
                                </Route>
                                <Route path="/Statistics">
                                    <Statistics />
                                </Route>
                                <Route path="/Download_Plugins">
                                    <DownloadPlugins />
                                </Route>
                                <Route path="/Faqs" component={Faqs} />
                                <Route path={["/", "/Dashboard"]} exact>
                                    <Dashboard />
                                </Route>
                            </Switch>
                        </div>
                    </div>
                )}
            </Data.Provider>
        </>
    );
}

export default Main;
