import React, { useContext } from "react";
// ICONS
import { FiSearch } from "react-icons/fi";
import { Redirect } from "react-router";
import { ReactComponent as Bell } from "../img/svg/Bell.svg";
import { ReactComponent as Hamburger } from "../img/svg/Hamburger.svg";
import { ReactComponent as Settings } from "../img/svg/Settings.svg";
import { User } from "./Auth/Auth";

function NavBar({ setOpen }) {
    const { user } = useContext(User);
    console.log(1);
    !user && <Redirect to="/login" />;
    return (
        <div className="NavBar container-fluid">
            <div className="row">
                <div className="logo main">
                    <h1 className="logo_text suprema font-weight-bold">ChatCloud</h1>
                    <div
                        className="navBarBtn icon-wrapper"
                        onClick={() => {
                            setOpen(true);
                        }}
                    >
                        <Hamburger color="white" />
                    </div>
                </div>
                <div className="search mobile">
                    <div className="searchbar">
                        <div className="searchBarConatiner">
                            <span className="searchIcon">
                                <FiSearch color="white" />
                            </span>
                            <input type="text" name="search" id="search" placeholder="Search" />
                        </div>
                    </div>
                    <div className="logo mobile">
                        <h1 className="logo_text suprema font-weight-bold">ChatCloud</h1>
                    </div>
                </div>
                <div className="utils">
                    <div className="notification">
                        <div className="notification-container icon-wrapper">
                            <Bell color="white" fill="white" />
                            <span className="count"></span>
                        </div>
                    </div>

                    <div className="profile">
                        <div className="profile-container icon-wrapper">
                            <Settings color="white" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default NavBar;
