import { useRef, useState, useContext, useEffect } from "react";
import LoginBg from "../img/Login_Bg.jpg";
import { Container, Form, Button, Spinner, Alert } from "react-bootstrap";
import { User } from "./Auth/Auth";
import { useHistory } from "react-router-dom";

function Login() {
    const [loading, setLoading] = useState(false);
    const [err, setErr] = useState("");
    const username = useRef();
    const password = useRef();
    const { login } = useContext(User);
    const history = useHistory();
    useEffect(() => {
        return () => {
            setErr("");
            setLoading(false);
        };
    }, []);

    async function handleLogin(e) {
        e.preventDefault();
        if (!username.current.value || !password.current.value)
            return setErr("Please Fill all Input Fields!");

        try {
            setLoading(true);
            setErr("");
            await login(username.current.value, password.current.value);
            history.push("/");
        } catch (error) {
            setErr("Failed to Sign in!");
        } finally {
            setLoading(false);
        }
    }
    return (
        <div className="login">
            <Container className="h-100">
                <div className=" h-100 px-5 py-3 pt-5 m-auto">
                    <div className="login-container mx-auto">
                        <h1 className="logo_text suprema font-weight-bold text-center">
                            ChatCloud
                        </h1>
                        <Form onSubmit={handleLogin}>
                            <h4
                                className={`display-4 text-center form_header ${
                                    err ? "mb-2" : "mb-5"
                                } `}
                            >
                                Sign in
                            </h4>
                            {err && <Alert variant="danger">{err}</Alert>}
                            <Form.Group>
                                <Form.Control
                                    type="text"
                                    id="username"
                                    autoComplete={"off"}
                                    required={true}
                                    ref={username}
                                />
                                <Form.Label htmlFor="username">username</Form.Label>
                            </Form.Group>
                            <Form.Group>
                                <Form.Control
                                    type="password"
                                    id="password"
                                    autoComplete={"off"}
                                    required={true}
                                    ref={password}
                                />
                                <Form.Label htmlFor="password">password</Form.Label>
                            </Form.Group>
                            <Button className="w-100 login_submit" disabled={loading} type="submit">
                                {loading ? <Spinner animation="border" color="white" /> : "SUBMIT"}
                            </Button>
                            <p className="text-center mt-3 forgot_pass">Forgot password?</p>
                        </Form>
                        <div className="form_footer text-center font-weight-bolder">
                            POWERED BY{" "}
                            <span>
                                <a href="http://chatcloud.co">CHATCLOUD</a>
                            </span>
                        </div>
                    </div>
                </div>
            </Container>
            <div className="image">
                <img src={LoginBg} alt="" />
            </div>
        </div>
    );
}

export default Login;
