import React, { useContext, useEffect } from "react";
import { IconContext } from "react-icons";
import { NavLink, Redirect } from "react-router-dom";
import { User } from "./Auth/Auth";
import { ReactComponent as Dashboard } from "../img/svg/Dashboard.svg";
import { ReactComponent as Conversations } from "../img/svg/Conversations.svg";
import { ReactComponent as Widgets } from "../img/svg/Widgets.svg";
import { ReactComponent as Settings } from "../img/svg/Settings.svg";
import { ReactComponent as Scripts } from "../img/svg/Scripts.svg";
import { ReactComponent as Statistics } from "../img/svg/Statistics.svg";
import { ReactComponent as Download } from "../img/svg/Download.svg";
import { ReactComponent as Faq } from "../img/svg/Faq.svg";
import { ReactComponent as LogOut } from "../img/svg/LogOut.svg";

import pic from "../img/image.jpg";
function Tabs({ setOpen }) {
    const { user, logOut } = useContext(User);

    const TabsArr = [
        {
            head: "Dashboard",
            link: "/",
            icon: <Dashboard />,
        },
        {
            head: "Conversation",
            link: "/Conversations",
            icon: <Conversations />,
        },
        {
            head: "Widgets",
            link: "/Widgets",
            icon: <Widgets />,
        },
        {
            head: "Options",
            link: "/Options",
            icon: <Settings />,
        },
        {
            head: "Scripts",
            link: "/Scripts",
            icon: <Scripts />,
        },
        {
            head: "Statistics",
            link: "/Statistics",
            icon: <Statistics />,
        },
        {
            head: "Download/Plugins",
            link: "/Download_Plugins",
            icon: <Download />,
        },
        {
            head: "FAQ",
            link: "/Faqs",
            icon: <Faq />,
        },
    ];
    const TabsJSX = TabsArr.map(({ head, link, icon }, index) => {
        return (
            <NavLink
                to={link}
                exact={link === "/Conversations" ? false : true}
                activeClassName="active"
                key={index}
                className="tab r-i-c"
                onClick={(e) => {
                    setOpen(false);
                }}
            >
                {icon}
                <h2>{head}</h2>
            </NavLink>
        );
    });

    return (
        <div className="routes h-100">
            <div className="container-fluid">
                <div className="profile">
                    <div className="profile_picture mb-2">
                        <img src={pic} alt="" />
                    </div>
                    <div className="profile_greeting mb-1 suprema">Welcome {user.firstname}!</div>
                    <div className="user_email">{user.email}</div>
                </div>
            </div>
            <div className="routes-wrapper">
                <div className="container-fluid position-static h-100">
                    <div className="row d-flex flex-column justify-content-between h-100">
                        <ul className="route-tabs list-group">
                            <span className="indicator"></span>
                            <IconContext.Provider value={{ size: 30 }}>
                                {TabsJSX}
                            </IconContext.Provider>
                        </ul>
                        <div className="logout_wrapper">
                            <div className="logout_btn r-i-c my-0" onClick={logOut}>
                                <LogOut size="20px" />
                                <p>Logout</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Tabs;
